import { Component } from '@angular/core';
import { AppmoduleService } from "./appmodule.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  started = "Game has not started yet :(";
  gameStarted = true;
  cardAmount;
  cards;

  constructor(private appmoduleService: AppmoduleService) { }

  public startGame() {
    // call server with service start game update start using websockets
    this.appmoduleService.startGame().subscribe((data: any[]) => {
      this.gameStarted = false;
      this.started = "Game has started :D";
      this.cards = data;
    });
  }

  public pickCard(card) {
    // get random card from server and display it i guess
    card.selected = true;
    console.log("picked card");
  }
}
